.navbar{
    background: #3d3d3d;;
    height: 5rem;
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    top: 0;
    z-index: 3;
    position: sticky;

}

.logo{
    object-fit: cover;
    height: 3rem;
    width: 3rem;

}
.desktopMenuList{
    margin: 0 2rem;
}



.desktopMenuListItem{
    margin: 1rem;
    cursor: pointer;

}

.desktopMenuListItem:hover{
    color: rgba(255, 166, 0, 0.879);
    padding-bottom: 0.5rem;
    border-bottom: 3px solid rgba(255, 166, 0, 0.879);

}


.desktopMenuBtn{
    background: white;
    border: none;
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
}
.desktopMenuImg{
    object-fit: cover;
    height: 1.5rem;
    width: 1.5rem;
    margin: 0.5rem;
}


.mobMenu{
    height: 3.5rem;
    width: 3.5rem;
    display: none;
}



.active{
    color: rgba(255, 166, 0, 0.879);
    padding-bottom: 0.5rem;
    border-bottom: 3px solid rgba(255, 166, 0, 0.879);

}

.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40, 40, 40);
    border-radius: 1rem;
}

.ListItem{
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30, 30, 30);
}

@media screen and (max-width:750px){
    .mobMenu{
        display: flex;
    }
    .desktopMenu{
        display: none;
    }
    .desktopMenuBtn{
        display: none;
    }
    
}