#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    /* margin: 0 auto; */
    margin: auto;
    height: 100vh;

}

.skillTitle{
    font-size:3rem ;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;

}

.skillDesc{
    font-size: 1rem;
    font-weight: 400;
    max-width: 50rem;
    padding: 0 2rem;
}

.skillBars{
    margin: 0.5em;
    width: 100vw;
    max-width: 80%;
    text-align: left;


}
.skillBar{
    display: flex;
  
    align-items: center;
    justify-content: center;
    margin: 1rem;
    padding: 1.5rem 5rem;
    border-radius: 2rem;
    background: rgb(50,50,50);


}

.skillBarImg{
    width: 5rem;
    height: 5rem;
    margin-right: 2rem;
}
.skillBarText{
    font-size: 1rem;
    font-weight: 200;
    color: inherit;
    text-decoration: none;

   
}

@media screen and (max-width:720px){
    .skillBarText>p{
        font-size: 2vw;
    }
    
    
}
@media screen and (max-width:480px){
    .skillBarText>p{
        font-size: 3vw;
    }
    .skillBarImg{
        height: 2rem;
        width: 2rem;
    }
    .skillBarText>h2{
        font-size: 5vw;
    }
    
}