#contact{
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contentPageTitle{
    font-size: 3rem;
    margin-bottom: 1rem;
}
.clientDesc{
    font-weight: 300;
    font-size: 2rem;

}
.clientImgs{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 1rem 0;


}

.clientImg{
    width: 20rem;
    /* height: 9rem; */
    object-fit: cover;
    width: 23%;
    min-width: 9rem;
    margin: auto;
    padding: 0.25rem;
    /* flex-wrap: wrap; */
}

#clients{
    padding: 2rem;
    

}

.contactDesc{
    padding: 1rem;
    font-size: medium;
    font-weight: 300;
}

.contactForm{

    margin: 1rem ;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;


}
.name, .email, .msg{
  font-size: medium;
  width: 100%;
  max-width: 40rem;
  margin: 0.5rem;
  padding: 0.5rem 1rem ;
  color:white;
  border-radius: 0.5rem;
  background: rgb(40,40,40);
  border: none;

}

.submitBtn{
    border: none;
    background: white;
    color: black;
    padding: 0.75rem 3.5rem;
    font-size: medium;
    margin: 2rem;

}

.links{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
margin: auto;
}

.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;

}